function extract(arr, t) {
    var retarr = [];
    for (var i = 0, len = arr.length; i < len; i++) {
        switch (t) {
            case 1:
                retarr.push(arr[i].x);
                break;
            case 2:
                retarr.push(arr[i].y);
                break;
            case 3:
                retarr.push(arr[i].z);
                break;
            default:
                break;
        }
    }
    return retarr;
}
//转置矩阵
function t_matrix(matrix) {
    var arr2 = [];
    for (var i = 0; i < matrix[0].length; i++) {
        arr2[i] = [];
    }
    for (var i = 0; i < matrix.length; i++) {
        for (var j = 0; j < matrix[i].length; j++) {
            arr2[j][i] = matrix[i][j];
        }
    }
    return arr2;
}
//最小值的下标
function argmin(list) {
    var min_index = 0;
    for (var i = 0, len = list.length; i < len; i++) {
        if (list[i] < list[min_index]) min_index = i;
    }
    return min_index;
}
//最优路径
function best_path(paths) {
    //    var i,j = paths.length,paths[0].length;
    var i = paths.length - 1;
    var j = paths[0].length - 1;
    var p = [];
    if (paths[i][j] != -1) p.push([i - 1, j - 1]);
    while (i > 0 && j > 0) {
        var c = argmin([paths[i - 1][j - 1], paths[i - 1][j], paths[i][j - 1]]);
        if (c == 0) {
            i = i - 1;
            j = j - 1;
        } else if (c == 1) {
            i = i - 1;
        } else if (c == 2) {
            j = j - 1;
        }
        if (paths[i][j] != -1) p.push([i - 1, j - 1]);
    }
    p.pop();
    //列表倒置
    var temp = 0;
    for (var k = 0; k < p.length / 2; k++) {
        temp = p[k];
        p[k] = p[p.length - 1 - k];
        p[p.length - 1 - k] = temp;
    }
    // console.log("after",p);
    return p;
}
//获取衰减系数
function get_common_seq(best_path, threshold) {
    var com_ls = [];
    var pre = best_path[0];
    var reslut_len = 1;
    for (var i = 0, len = best_path.length; i < len; i++) {
        if (i == 0) continue;
        var cur = best_path[i];
        if (cur[0] == (pre[0] + 1) && cur[1] == (pre[1] + 1)) {
            reslut_len += 1;
        } else {
            com_ls.push(reslut_len);
            reslut_len = 1;
        }
        pre = cur;
    }
    com_ls.push(reslut_len);

    var reslut_list = [];
    for (var i = 0, len = com_ls.length; i < len; i++) {
        if (com_ls[i] > threshold) reslut_list.push(com_ls[i]);
    }
    // console.log("reslut_list",reslut_list);
    return reslut_list;
}
//
function calculate_attenuate_weight(seqLen, com_ls) {
    var weight = 0;
    for (var i = 0, len = com_ls.length; i < len; i++) {
        // console.log("wei",(com_ls[i] * com_ls[i] ) / (seqLen * seqLen));
        weight = weight + (com_ls[i] * com_ls[i]) / (seqLen * seqLen);
    }
    // console.log("wei2",Math.sqrt(weight));
    return 1 - Math.sqrt(weight);
}

function dtw(tpl_arr, tar_arr, xsE) {
    var len1 = tpl_arr.length;
    var len2 = tar_arr.length;
    var paths = [];
    for (var i = 0; i < len1 + 1; i++) {
        var row = [];
        for (var j = 0; j < len2 + 1; j++) {
            row.push(inf);
        }
        paths.push(row);
    }
    paths[0][0] = 0;
    for (var k = 0; k < len1; k++) {
        var d = 0;
        var cost = 0;
        for (var n = 0; n < len2; n++) {
            d = tpl_arr[k] - tar_arr[n];
            cost = d * d;
            paths[k + 1][n + 1] = cost + Math.min(paths[k][n + 1], paths[k + 1][n], paths[k][n]);
        }
    }
    // console.log("paths",JSON.stringify(paths));
    for (var i = 0, len = paths.length; i < len; i++) {
        for (var j = 0, lenx = paths[i].length; j < lenx; j++) {
            paths[i][j] = Math.sqrt(paths[i][j]).toFixed(2);
        }
    }
    var distance = paths[len1][len2];
    //转置
    paths = t_matrix(paths);
    //best_path
    var best_path_1 = best_path(paths);
    //衰减系数
    var com_ls1 = get_common_seq(best_path_1, 1);
    //生成权重
    // console.log("best_path_1.length",best_path_1.length);
    // console.log("com_ls1",com_ls1);
    var weight = calculate_attenuate_weight(best_path_1.length, com_ls1);
    // console.log("distance",distance);
    // console.log("weight",weight);
    // return distance*weight;
    //限定在0~1之间
    // console.log("distance",distance);
    var xsdx = (distance * weight).toFixed(2);
    //  console.log("xsdx",xsdx);
    if (xsdx <= xsE) return 1;
    var n = 4.0;
    if (xsdx >= n * xsE) return 0;

    var k = 1 / (xsE * (1 - n));
    var b = n / (n - 1);
    //return (xsdx-n*xsE)/(xsE*(1-n));
    return (1.0 / (xsE * (1 - n))) * xsdx + n / (n - 1)



}

function analysis(tpldata, tardata) {

    var ahrs_xsd_x = dtw(extract(tpldata, 1), extract(tardata, 1), attributes.ahrs_x_weight);
    var ahrs_xsd_y = dtw(extract(tpldata, 2), extract(tardata, 2), attributes.ahrs_y_weight);

    // console.log("ahrs_xsd_x",ahrs_xsd_x);
    // console.log("ahrs_xsd_y",ahrs_xsd_y);
    return (ahrs_xsd_x * 0.8 + ahrs_xsd_y * 0.2).toFixed(2);
}

function analysisSpeed(tpldata, tardata) {

    var ahrs_xsd_x = dtw(tpldata, tardata, attributes.speed_weight);

    return ahrs_xsd_x.toFixed(2);
}

module.exports = {
    analysis: analysis,
    analysisSpeed: analysisSpeed,
}