/**
 * 对数据进行整理，用于做相似度对比
 */
function formatData(teacherData, studentData) {

  console.log("初始时 studentData:", studentData, " 长度：", studentData.length);
  console.log("初始时 teacherData:", teacherData, " 长度：", teacherData.length);

  // 找到速度最大值
  let maxIndexStu = 0; // 学生最大值下标
  let maxIndexTeach = 0; //教师数据最大值下标
  let maxValue = studentData[0];
  let maxValueTeach = teacherData[0];
  for (let j = 0; j < studentData.length; j++) {
    if (j > 0) {
      if (maxValue < studentData[j]) {

        // 最大值的下标
        maxIndexStu = j;
        maxValue = studentData[j];
      }
    }
  }

  for (let j = 0; j < teacherData.length; j++) {
    if (j > 0) {
      if (maxValueTeach < teacherData[j]) {

        // 最大值的下标
        maxIndexTeach = j;
        maxValueTeach = teacherData[j];
      }
    }
  }

  // console.log("学生最大值下标:", maxIndexStu);
  // console.log("老师最大值下标:", maxIndexTeach);
  let value = maxIndexStu - maxIndexTeach; // 相差
  // 循环添加数组里的元素或者去掉数组的元素
  for (let j = 0; j < Math.abs(value); j++) {

    // 添加
    if (value < 0) {
      studentData.unshift(""); ///添加要改
    } else { // 减去
      studentData.splice(0, 1);
    }
  }

  //若两者长度不一样，就截取


  if (studentData.length > teacherData.length) {

    let len = studentData.length - teacherData.length;
    console.log("减 len:", len);

    studentData.splice(teacherData.length, len);
  } else if (studentData.length < teacherData.length) {

    let len = teacherData.length - studentData.length;
    console.log("加 len:", len);

    for (let i = 0; i < len; i++) {
      studentData.push(0);
    }
  }


  /** 将学生发力曲线最高点对齐老师的 end */

  console.log("对齐时 studentData:", studentData, " 长度：", studentData.length);
  console.log("对齐时 teacherData:", teacherData, " 长度：", teacherData.length);

}

module.exports = {
  formatData: formatData,
}