import util from './util.js';
import md5 from './md5.js';

const apiUrl = "https://teach.szearth.cn/"; //开发时使用本地接口，在上线时只需要修改此处接口为线上地址即可

const request = (url, method, p) => {

	console.log("请求地址===>>" + apiUrl + url + p.action);
	var fieldToString = JSON.stringify(p); //将json值转换成json字符串
	var fieldToBase64 = util.baseEncode(fieldToString); //将字符串进行base64编码

	return new Promise((resolve, reject) => {
		uni.request({
			// url: BASE_URL + options.url,
			// method: options.methods || "GET",
			// data: options.data || {},
			// dataType: options.dataType || "json",
			url: apiUrl + url,
			data: fieldToBase64,
			header: {
				'content-type': 'application/json'
			},
			method: method,
			dataType: 'json',
			responseType: 'text',
			success: (res) => {
				if (res.statusCode == 200) {
					resolve(res.data);
				} else {
					wx.showToast({
						title: '服务异常',
						icon: 'none',
					})
				}
				resolve(res)
			},
			fail: (err) => {
				reject(err)
			}
		})
	})
}

const verify = (action, params) => {

	let time = Date.parse(new Date());
	let field = {
		"action": action,
		"time": time,
		"ip": "admin",
		"params": {}
	}
	//验签
	let sign_src = util.get_sign_src(field);
	let token = "ji3kplqimc23l1k51kdh";
	sign_src = sign_src + token;
	let server_sign = md5.md5(sign_src).toLowerCase(); //服务器端的验签是小写字母

	field = {
		"action": action,
		"time": time,
		"ip": "admin",
		"sign": server_sign,
		"params": params
	}
	return field;

}


export default {
	request: request,
	verify: verify,
}
