//绘制柱形图
function initBar(chart, xAxis, yAxis) {

  var option = {
    tooltip: {
      trigger: 'axis',
      axisPointer: {            // 坐标轴指示器，坐标轴触发有效
        type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
      },
      backgroundColor: 'rgba(97, 127, 251, 0.8)',
      padding: [5, 20, 5, 10],//上 右 下 左
      confine: true,//将 tooltip 限制在该容器
      formatter: function (params) {

        let index = params[0].dataIndex;
        let htmlStr = '';
        let like = xAxis[index];
        let take = yAxis[index];
        if (!take) {
          take = 0;
        }
        htmlStr += '百分比：' + take + "%" + '\n' + '相似度：' + like + '%' + "-" + (parseInt(like) + 10) + '%';
        return htmlStr;
      }
    },
    grid: {
      left: '3%',
      right: '4%',
      bottom: '15%',
      height: '80%',
      containLabel: true,
    },
    xAxis: [
      //因为一般正常设置，只能一个刻度对应一个值。若需要在范围区间显示一个值，就需要使用两个x轴来解决
      {
        data: yAxis,
        show: false
      },
      {
        data: xAxis,
        axisLabel: {
          interval: 0,
          show: true,
        },
        position: 'bottom',
        boundaryGap: false,
        axisPointer: {
          show: false,
        },
        axisLine: {
          lineStyle: {
            type: 'solid',
            color: '#D2D2D2',//左边线的颜色
          }
        },
      },
    ],
    yAxis: {
      type: 'value',
      axisLine: {
        lineStyle: {
          type: 'solid',
          color: '#98B1FF',//左边线的颜色
        }
      },
      splitLine: { show: false },//去除网格线

    },
    series: [{
      type: 'bar',
      data: yAxis,
      itemStyle: {
        normal: {
          color: '#98B1FF',
        }
      },
      barCategoryGap: '0%'
    }],
  }

  chart.setOption(option, true);

}


export default {
	formatTime: formatTime,
	baseEncode: baseEncode,
	baseDecode: baseDecode,
	get_sign_src: get_sign_src,
	md5: md5,
	initChartOne: initChartOne,//单轴曲线
	initBar: initBar,//柱形图
	initSingle: initSingle,//单轴双曲线
	findcode: findcode,
}

